@tailwind base;
@tailwind components;
@tailwind utilities;

@import 'tailwindcss/base';

@import 'tailwindcss/components';

@import 'tailwindcss/utilities';
@import '~ng-zorro-antd/ng-zorro-antd.min.css';

span,
body {
  font-size: 12px;
}
/* Scroll bar stylings */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--lightestgrey);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #cccccc;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #8a8a8a;
}

.ngx-datatable .datatable-body-cell-label {
  font-size: 12px;
}

.ant-form-item {
  margin-bottom: 12px !important;
  .ant-form-item-label {
    label {
      @apply text-xs font-semibold;
    }
  }
}

nz-modal-container {
  .ant-modal {
    max-height: calc(100vh - 200px) !important;
    display: flex;

    .ant-modal-content {
      display: flex;
      width: 100%;
      flex-direction: column;

      .ant-modal-body {
        overflow: auto;
      }
    }
    .ant-modal-footer {
      display: flex;
    }
  }
}

nz-tabset {
  height: 100%;
  .ant-tabs-content {
    height: 100%;
  }
}

// Table
ngx-datatable {
  datatable-header {
    min-height: 50px;

    datatable-header-cell {
      span {
        font-weight: 600 !important;
        font-size: 13px;
        color: #333333;
      }
    }
  }
  div.visible {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  datatable-body {
    overflow: auto;
  }

  datatable-footer {
    min-height: 50px;
    overflow: hidden !important;
  }
}

button {
  border-radius: 4px !important;
  display: flex !important;
  align-items: center !important;
}

nz-form-label {
  width: 200px !important;
}

.ant-form-vertical nz-form-label {
  width: 100% !important;
}

icom-libs-product-list formly-form formly-field formly-group {
  display: flex;
}

icom-libs-inventory-items-list formly-form formly-field formly-group {
  display: flex;
}

icom-libs-product-list formly-form formly-field formly-group {
  display: flex;
}
icom-libs-inventory-customer formly-form formly-field formly-group {
  display: flex;
}

icom-libs-campaign-transaction-list formly-form formly-field formly-group {
  display: flex;
}

// app-custom-form formly-form formly-field formly-group {
